/* eslint-disable no-undef */
import React from 'react';
import ScriptTag from 'react-script-tag';
import logo from './logo.svg';
import precor from './precorJs.js';
import './App.css';

function App() {
  return (
    <div className="App">
      <ScriptTag type="text/javascript" src={ precor } />
      <h1>Workout App Precor ltd</h1>
      <button onClick={() => { document.getElementById("output").innerHTML = precorWorkout.getWorkoutLimits() }}>getWorkoutLimits</button>
      <button onClick={() => { document.getElementById("output").innerHTML = precorWorkout.getState() }}        >getState</button>
      <br/>
      <button onClick={() => { precorWorkout.setSpeed(2) }}                                                  >setSpeed</button>
      <button onClick={() => { precorWorkout.setResistance(2) }}                                                >setResistance</button>
      <button onClick={() => { precorWorkout.setIncline(2)   }}                                                >setIncline</button>
      <button onClick={() => { precorWorkout.setRamp(2)      }}                                                >setRamp</button>
      <br/>
      <button onClick={() => { precorWorkout.cancelPreWorkoutPhase()}}                                         >cancelPreWorkoutPhase</button>
      <button onClick={() => { precorWorkout.finishPreWorkoutPhase()}}                                         >finishPreWorkoutPhase</button>
      <button onClick={() => { precorWorkout.finishWorkoutPhase()}}                                            >finishWorkoutPhase</button>
      <button onClick={() => { precorWorkout.finishPostWorkoutPhase()}}                                        >finishPostWorkoutPhase</button>
      <button onClick={() => { precorWorkout.setHidePrecorSummaryAfterWorkout(true)}}                          >setHidePrecorSummaryAfterWorkout(true)</button>
      <button onClick={() => { precorWorkout.setHidePrecorSummaryAfterWorkout(false)}}                         >setHidePrecorSummaryAfterWorkout(false)</button>
      <button onClick={() => { precorWorkout.setBarMessage("THIS IS BAR MESSAGE 2", 1)}}                               >setBarMessage</button>
      <button onClick={() => { precorWorkout.setFeaturedWorkout(JSON.stringify({name: "My Custom Workout", provider: "provider", category: "category"}))}}>setFeaturedWorkout</button>
      <button onClick={() => { precorWorkout.setFeaturedWorkout("")}}>setFeaturedWorkout("")</button>
      <button onClick={() => { precorWorkout.setQuickstartWorkout(JSON.stringify({name: "My Custom Workout", provider: "provider", category: "category"}))}}>setQuickstartWorkout</button>
      <button onClick={() => { precorWorkout.requestSignIn()}}                                                 >requestSignIn</button>
      <button onClick={() => { precorWorkout.disableMenus();  document.getElementById("output").innerHTML = "disableMenus"}}>disableMenus</button>
      <button onClick={() => { precorWorkout.setDisableMachineControls(true)}}                                 >setDisableMachineControls(true)</button>
      <button onClick={() => { precorWorkout.setDisableMachineControls(false)}}                                >setDisableMachineControls(false)</button>

      <button onClick={() => { document.getElementById("output").innerHTML = precorWorkout.getMetrics()}}      >getMetrics</button>
      <button onClick={() => { document.getElementById("output").innerHTML = precorWorkout.getSummaryMetrics()}}>getSummaryMetrics</button>
      <button onClick={() => { document.getElementById("output").innerHTML = precorWorkout.getFitnessEquipmentInfo()}}>getFitnessEquipmentInfo</button>
      <br/>
      <textarea id='output' cols='60' rows='6'></textarea>
    </div>
  );
}

export default App;
